.container {
  max-width: 1460px;
  margin: 0 auto;
  padding: 0 10px; }

@font-face {
  font-family: "Larsseit-Bold";
  src: url("./../fonts/woff2/Larsseit-Bold.woff2") format("woff2"), url("./../fonts/woff/Larsseit-Bold.woff") format("woff"); }

@font-face {
  font-family: "Larsseit-Medium";
  src: url("./../fonts/woff2/Larsseit-Medium.woff2") format("woff2"), url("./../fonts/woff/Larsseit-Medium.woff") format("woff"); }

@font-face {
  font-family: "Larsseit";
  src: url("./../fonts/woff2/Larsseit.woff2") format("woff2"), url("./../fonts/woff/Larsseit.woff") format("woff"); }

.thrive__wrapper {
  display: flex; }

.thrive__content {
  padding: 160px 181px 95px;
  background-color: #62D0DF; }

.thrive__title {
  font-family: Larsseit-Bold;
  font-size: 36px;
  line-height: 27.42px;
  letter-spacing: -0.6%;
  color: #FFFFFF;
  margin-bottom: 75px; }

.thrive__span {
  font-family: Larsseit;
  font-size: 24px;
  line-height: 35.16px;
  text-align: left;
  color: #FFFFFF; }

.thrive__text {
  font-family: Larsseit;
  font-size: 20px;
  line-height: 29.3px;
  text-align: left;
  color: #FFFFFF; }

.about__wrapper {
  max-width: 1230px;
  padding-top: 150px;
  padding-bottom: 25px;
  margin: 0 auto;
  position: relative; }

.about__title {
  font-family: Larsseit;
  font-size: 16px;
  line-height: 22.5px;
  letter-spacing: 6%;
  color: #22356F;
  margin-top: 5px;
  position: absolute; }

.about__title-link {
  text-decoration: none; }

.about__content {
  display: flex;
  flex-direction: column;
  align-items: center; }

.about__text {
  max-width: 590px;
  font-family: Larsseit-Medium;
  font-size: 24px;
  text-align: left;
  line-height: 35.75px;
  letter-spacing: 0.5%;
  color: #22356F; }

.about__link {
  font-family: Larsseit;
  font-size: 16px;
  line-height: 18.75px;
  letter-spacing: 6%;
  color: #22356F;
  opacity: 0.5;
  margin-top: 75px;
  text-decoration: none; }

.training__wrapper {
  height: 480px;
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-template-rows: 360px; }

.training__cell {
  padding-top: 105px;
  position: relative;
  font-family: Larsseit-Bold;
  font-size: 32px;
  text-align: center;
  line-height: 33.75px;
  letter-spacing: -0.6%;
  color: #FFFFFF; }
  .training__cell::before {
    content: "";
    background-image: url(./../images/training/circle.png);
    background-position: center;
    background-repeat: no-repeat;
    width: 9px;
    height: 9px;
    position: absolute;
    left: 54px;
    top: 114px; }
  .training__cell::after {
    content: "";
    width: 255px;
    height: 1px;
    background-color: #FFFFFF;
    position: absolute;
    top: 75px;
    left: 52.5px; }
  .training__cell--pink {
    background: #FFE2DE; }
  .training__cell--lightgreen {
    background: #C4E769;
    text-align: left;
    padding-left: 78px;
    padding-right: 50px; }
  .training__cell--lightblue {
    background: #62D0DF; }
  .training__cell--blue {
    background: #0052C1; }

.training__link {
  text-decoration: none;
  position: relative; }

.method__wrapper {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-template-rows: repeat(2, 480px); }

.method__cell {
  padding-top: 134px;
  padding-left: 104px;
  position: relative;
  font-family: Larsseit-Bold;
  font-size: 32px;
  text-align: left;
  line-height: 33.75px;
  letter-spacing: -0.6%;
  color: #FFFFFF; }
  .method__cell::before {
    content: "";
    background-image: url(./../images/method/circle.png);
    background-position: center;
    background-repeat: no-repeat;
    width: 12px;
    height: 12px;
    position: absolute;
    left: 72px;
    top: 143px; }
  .method__cell::after {
    content: "";
    width: 340px;
    height: 1px;
    background-color: #FFFFFF;
    position: absolute;
    top: 95px;
    left: 70px; }
  .method__cell--darkblue {
    background: #22356F;
    padding-right: 200px; }
  .method__cell--blue {
    background: #0052C1;
    padding-right: 200px; }
  .method__cell--smoothie {
    background-image: url(./../images/method/smoothie.png);
    background-position: center;
    background-repeat: no-repeat;
    max-width: 480px;
    width: 100%;
    object-fit: cover; }
  .method__cell--lightblue {
    background: #62D0DF; }
  .method__cell--lilac {
    background: #DEE1FF; }
  .method__cell--violet {
    background: #8F00FF; }

.method__link {
  text-decoration: none;
  position: relative; }
